
import { Item } from '../model/item';
import { ItemView } from './Item';
import { Navbar } from './navbar';
import './App.css';
import { itemDataList } from '../parseJSON'

function App() {

  let newItem = new Item("A New Hope", "film", "NC")

  const itemList = itemDataList.splice(0, 1000);

  const itemListView = itemList.map((item) => 
    <li key={item.id + 1}>
      <div key={item.id}><ItemView data={item}/></div>
      <hr />
    </li>
  );

  return (
    <div className="App">

      <Navbar />

      <br />

      <div className="bar"></div>
      
      <ul className="itemList">{itemListView}</ul>      

    </div>
  );
}

export default App;
